<template>
  <page
    @on-ready="onReady"
    @on-show="onShow"
    ref="page"
    class="create-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar slot="header" id="header" title="故障提报" left-arrow @click-left="onClickLeft"></van-nav-bar>

    <van-cell-group>
      <van-cell required title="所属专业" is-link clickable @click="handleSelectProfession">
        <span>{{ profession && profession.name }}</span>
      </van-cell>
      <van-cell title="发生时间" is-link clickable @click="handleSelectTime">
        <span>{{ time }}</span>
      </van-cell>
    </van-cell-group>

    <van-cell-group>
      <van-cell required title="故障地点" is-link clickable @click="handleSelectLine">
        <span>{{ line && line.name }} / {{ station && station.name }}</span>
      </van-cell>
      <van-field v-model="address" placeholder="请输入详细地址" type="textarea" :rows="2" autosize />
      <van-cell required title="故障设备" is-link clickable @click="handleSelectDevice"
        >{{ deviceType && deviceType.name }} / {{ device && device.name }}</van-cell
      >
    </van-cell-group>

    <van-cell-group>
      <van-cell title="故障分类" is-link clickable @click="handleSelectReason">
        <span>{{ reason && reason.name }}</span>
      </van-cell>
      <!-- <van-cell title="故障类型" is-link clickable @click="handleSelectType">
        <span>{{ type && type.name }}</span>
      </van-cell> -->
      <van-cell title="故障等级" is-link clickable @click="handleSelectLevel">
        <span>{{ level && level.name }}</span>
      </van-cell>
      <van-cell title="故障来源" is-link clickable @click="handleSelectSource">
        <span>{{ source && source.name }}</span>
      </van-cell>
    </van-cell-group>

    <van-cell-group>
      <van-cell required class="detail-title" title="故障现象："></van-cell>
      <van-field v-model="content" placeholder="请输入现象" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title" title="图像："></van-cell>
      <div class="images-container">
        <div class="image-item-show" v-for="(item, index) in imageList" :key="index" @click="handlePlayImages(index)">
          <!-- <img :src="item.url" @click="handlePlayImages(index)"> -->
          <auto-image
            :src="item.url"
            :width="108"
            :height="108"
            style="margin-bottom: 12px;margin-left: 12px;"
          ></auto-image>
          <div class="image-remove" @click.stop="handleImageRemove(index)"></div>
        </div>
        <div class="image-item" style="background: #fff;" @click.stop="handlePhoto1">
          <img src="../../assets/images/image-add.png" />
        </div>
      </div>
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title" title="视频："></van-cell>
      <div class="images-container">
        <div class="image-item-show" v-for="(item, index) in videoList" :key="index" @click="handlePlayVideo(item)">
          <!-- <auto-image :src="item.url" :width="108" :height="108" style="margin-bottom: 12px;margin-left: 12px;"></auto-image> -->
          <img src="../../assets/images/video-default.png" class="img-video" />
          <div class="image-remove" @click.stop="handleVideoRemove(index)"></div>
        </div>
        <div class="image-item" style="background: #fff;" @click.stop="handlePhoto2">
          <img src="../../assets/images/image-add.png" />
        </div>
      </div>
    </van-cell-group>

    <div class="blank-empty"></div>

    <template slot="blank">
      <div class="footer-action">
        <van-button class="action-item-main"  @click="handleSave">保存</van-button>
        <van-button class="action-item-main" color="#108EE9" @click="handleSubmit">提交</van-button>
      </div>

      <van-popup
        v-model="professionShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择所属专业</div>
        <a-scroll ref="filterScroller" v-if="professionShow" class="filter-scroll">
          <van-radio-group v-model="professionRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                :label="item.description"
                clickable
                @click="professionRadio = {...item}"
                v-for="(item) in professionOptions"
                :key="item.id"
              >
                <van-radio slot="right-icon" :name="item.id"/>
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onProfessionSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="sourceShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择故障来源</div>
        <a-scroll ref="filterScroller" v-if="sourceShow" class="filter-scroll">
          <van-radio-group v-model="sourceRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                :label="item.description"
                clickable
                @click="sourceRadio = {...item}"
                v-for="(item, index) in sourceOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onSourceSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="reasonShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择故障分类</div>
        <a-scroll ref="filterScroller" v-if="reasonShow" class="filter-scroll">
          <van-radio-group v-model="reasonRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                :label="item.description"
                clickable
                @click="reasonRadio = {...item}"
                v-for="(item, index) in reasonOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onReasonSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="typeShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择故障类型</div>
        <a-scroll ref="filterScroller" v-if="typeShow" class="filter-scroll">
          <van-radio-group v-model="typeRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                :label="item.description"
                clickable
                @click="typeRadio = {...item}"
                v-for="(item, index) in typeOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onTypeSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="levelShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择故障等级</div>
        <a-scroll ref="filterScroller" v-if="levelShow" class="filter-scroll">
          <van-radio-group v-model="levelRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                :label="item.description"
                clickable
                @click="levelRadio = {...item}"
                v-for="(item, index) in levelOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" @click="levelRadio = item" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onLevelSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup v-model="timeShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          v-model="currentTime"
          type="datetime"
          @confirm="handleConfirm"
          @cancel="timeShow = false"
          :formatter="formatter"
        />
      </van-popup>

      <van-dialog v-model="editShow" show-cancel-button :before-close="beforeClose">
        <van-cell title="描述"></van-cell>
        <van-field ref="desInput" v-model="description" type="textarea" :clearable="true" />
      </van-dialog>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import AScroll from '../../components/Scroll'
import navigate from '../../utils/navigate.js'
import AutoImage from '../../components/AutoImage'
import db from '../../db'
import io from '../../api'
import moment from 'moment'
import native from '../../utils/native.js'
import { uploadImage, uploadVideo } from '../../utils/upload.js'

export default {
  components: {
    Page,
    AScroll,
    AutoImage
  },
  data: () => ({
    headerHeightVW: 0,
    levelOptions: JSON.parse(localStorage.getItem('level_options')),
    reasonOptions: JSON.parse(localStorage.getItem('reason_options')),
    typeOptions: JSON.parse(localStorage.getItem('type_options')),
    professionOptions: JSON.parse(localStorage.getItem('profession_options')),
    sourceOptions: JSON.parse(localStorage.getItem('source_options')),
    profession: null,
    source: null,
    level: null,
    device: null,
    type: null,
    reason: null,
    reasonShow: false,
    reasonRadio: null,
    sourceShow: false,
    sourceRadio: null,
    typeShow: false,
    typeRadio: null,
    levelShow: false,
    levelRadio: null,
    professionShow: false,
    professionRadio: null,
    imageList: [],
    videoList: [],
    UIPhotoViewer: null,
    editShow: false,
    description: '',
    imgFile: '',
    repairContent: '',
    repairRemark: '',
    currentTime: new Date(),
    timeShow: false,
    time: moment().format('YYYY-MM-DD HH:mm'),
    line: null,
    station: null,
    lineShow: false,
    lineColumns: [],
    address: '',
    content: '',
    device: null,
    deviceType: null,
    localId: null,
    imageFilter: null,
    videoCompression: null
  }),
  methods: {
    onReady(header, main) {
      this.header = header

      this.headerHeightVW = ((this.header.h + 1) * 100) / this.header.w

      localStorage.setItem('device_select', '')
      localStorage.setItem('device_type_select', '')
      localStorage.setItem('address_select', '')

      const detailId = navigate.getParam('detail')

      if (detailId) {
        db.getFaultDetail(detailId).then(data => {

          this.profession = data.profession
          this.time = data.time
          this.line = data.line
          this.station = data.station
          this.address = data.address
          this.reason = data.reason
          this.type = data.type
          this.level = data.level
          this.source = data.source
          this.content = data.content
          this.imageList = data.images
          this.videoList = data.videos
          this.device = data.deviceId
          this.deviceType = data.deviceCategory
          this.localId = data.id
        })
      }
    },

    onShow() {
      if (localStorage.getItem('device_select')) {
        this.device = JSON.parse(localStorage.getItem('device_select'))

        localStorage.setItem('device_select', '')
      }
      
      if (localStorage.getItem('device_type_select')) {
        this.deviceType = JSON.parse(localStorage.getItem('device_type_select'))

        localStorage.setItem('device_type_select', '')
      }

      if (localStorage.getItem('address_select')) {
        const address = JSON.parse(localStorage.getItem('address_select'))

        this.line = address[0]

        this.station = address[address.length - 1]
      }
    },

    onClickLeft() {
      // navigate.back()
      this.$dialog.confirm({
        message: '返回数据将丢失，是否确认返回？',
        confirmButtonText: '确认',
        cancelButtonText: '取消'
      }).then(() => {
        navigate.back()
      }).catch(() => {})
    },

    handleSelectTime() {
      this.timeShow = true
    },

    handleConfirm(time) {
      this.time = moment(time).format('YYYY-MM-DD HH:mm')
      this.timeShow = false
    },

    handleSelectReason() {
      this.reasonShow = true
      this.reasonOptions = JSON.parse(localStorage.getItem('reason_options')).filter(
        item => item.profession.findIndex(p => this.profession && p == this.profession.id) >= 0
      )
      this.reasonRadio = { ...this.reason }
    },

    onReasonSelect() {
      this.reason = { ...this.reasonRadio }
      this.reasonShow = false
    },

    handleSelectSource() {
      this.sourceShow = true
      this.sourceRadio = { ...this.reason }
    },

    onSourceSelect() {
      this.source = { ...this.sourceRadio }
      this.sourceShow = false
    },

    handleSelectType() {
      this.typeShow = true
      this.typeOptions = JSON.parse(localStorage.getItem('type_options')).filter(
        item => item.profession.findIndex(p => this.profession && p == this.profession.id) >= 0
      )
      this.typeRadio = { ...this.type }
    },

    onTypeSelect() {
      this.type = { ...this.typeRadio }
      this.typeShow = false
    },

    handleSelectLevel() {
      this.levelShow = true
      this.levelOptions = JSON.parse(localStorage.getItem('level_options')).filter(
        item => item.profession.findIndex(p => this.profession && p == this.profession.id) >= 0
      )
      this.levelRadio = { ...this.level }
    },

    onLevelSelect() {
      this.level = { ...this.levelRadio }
      this.levelShow = false
    },

    handleSelectProfession() {
      this.professionShow = true
      this.professionRadio = { ...this.profession }
    },

    onProfessionSelect() {
      this.profession = { ...this.professionRadio }
      this.professionShow = false
    },

    handlePhoto1() {
      api.actionSheet({
        buttons: ['打开照相机', '从相册获取']
      }, (ret, err) => {
        var index = ret.buttonIndex;
        let sourceType = ''
        if (index === 1) {
          sourceType = 'camera'
        } else if (index === 2) {
          sourceType = 'album'
        }
        sourceType && api.getPicture(
          {
            sourceType: sourceType,
            destinationType: 'url',
            preview: true,
            quality: 80,
            mediaValue: 'pic',
            // saveToPhotoAlbum: true
          },
          (ret, err) => {
            // alert(JSON.stringify(ret))

            if (ret.data) {

              this.imgFile = ret.data
              this.description = ''
              this.editShow = true
              setTimeout(() => {
                this.$refs.desInput.focus()
              }, 200)
            }
          }
        )
      })
    },

    handlePhoto2() {
      api.actionSheet({
        buttons: ['打开照相机', '从相册获取']
      }, (ret, err) => {
        var index = ret.buttonIndex;
        let sourceType = ''
        if (index === 1) {
          sourceType = 'camera'
        } else if (index === 2) {
          sourceType = 'album'
        }

        sourceType && api.getPicture(
          {
            sourceType: sourceType,
            destinationType: 'url',
            preview: true,
            quality: 100,
            mediaValue: 'video',
            saveToPhotoAlbum: true,
            videoQuality: 'high',
          },
          (ret, err) => {
            if (ret.data) {

              if (api.systemType == 'ios') {
                this.$toast.loading({
                  duration: 0,
                  forbidClick: true,
                  loadingType: 'spinner',
                  message: '正在压缩...'
                })

                if (!this.videoCompression) {
                  this.videoCompression = api.require('videoCompression')
                }

                this.videoCompression.compression({
                  path: ret.data,
                  quality: 'medium'
                },(ret1, err1) => {
                  if (ret1.eventType == 'completed') {
                    this.videoList.push({ url: ret1.path })
                    this.$toast.clear()
                  }
                })
              } else {
                this.videoList.push({ url: ret.data })
              }
            }

            // if (ret.data) {

            //   const fileExtension = ret.data.substring(ret.data.lastIndexOf('.') + 1)

            //   const filePath = 'fs://hsvideo/' + new Date().getTime() + '.' + fileExtension

            //   alert(ret.data)
            //   alert(filePath)

            //   api.download({
            //     url: ret.data,
            //     savePath: filePath,
            //     report: true,
            //     cache: true,
            //     allowResume: true
            //   }, (ret, err) => {
            //     alert(JSON.stringify(ret))
            //     if (ret.savePath && ret.state == 1) {
            //       this.videoList.push({url: ret.savePath})
            //     }
            //   })

            // }
          }
        )
      })

      
    },

    beforeClose(action, done) {
      if (action === 'confirm') {
        this.imageList.push({
          url: this.imgFile,
          description: this.description
        })
      } else {
        this.imageList.push({ url: this.imgFile, description: '' })
      }
      done()
    },

    handleVideoRemove(index) {
      this.videoList.splice(index, 1)
    },

    handleImageRemove(index) {
      this.imageList.splice(index, 1)
    },

    handlePlayVideo(item) {
      api.openVideo({
        url: item.url
      })
    },

    handlePlayImages(index) {
      localStorage.setItem('photos', JSON.stringify(this.imageList))
      navigate.open('photoViewer', { index: index })
    },

    handleSelectDevice() {
      navigate.open('device', {profession: (this.profession && this.profession.id) ? this.profession.id : ''} )
    },

    handleSelectLine() {
      navigate.open('address')
    },

    handleSubmit() {
      if (this.profession == null || this.line == null || this.station == null || this.content == '' || !this.deviceType) {
        this.$toast.fail('请填写所有必填项')
      } else {
        this.$toast.loading({
          duration: 0,
          forbidClick: true,
          loadingType: 'spinner',
          message: '正在提交...'
        })

        if (native.connection()) {
          uploadImage(this.imageList).then(s => {
            this.imageList = s

            uploadVideo(this.videoList).then(v => {
              this.videoList = v

              io.createFault({
                profession: this.profession ? this.profession.id : '',
                time: this.time,
                line: this.line ? this.line.id : '',
                station: this.station ? this.station.id : '',
                address: this.address,
                reason: this.reason ? this.reason.id : '',
                type: this.type ? this.type.id : '',
                level: this.level ? this.level.id : '',
                source: this.source ? this.source.id : '',
                content: this.content,
                images: JSON.stringify(this.imageList),
                videos: JSON.stringify(this.videoList),
                deviceId: this.device ? this.device.id : '',
                deviceCategory: this.deviceType ? this.deviceType.id : ''
              })
                .then(data => {
                  if (this.localId) {
                    db.removeMyFault(this.localId).then(() => {
                      // navigate.open('my_fault')
                      navigate.back()
                    })
                  } else {
                    navigate.open('my_fault')
                    // navigate.back()
                  }
                })
                .catch(msg => {
                  this.$toast(msg)
                })
            })
          })
        } else {
          if (this.localId) {
            db.updateMyFault({
              profession: this.profession,
              time: this.time,
              line: this.line,
              station: this.station,
              address: this.address,
              reason: this.reason,
              type: this.type,
              level: this.level,
              source: this.source,
              content: this.content,
              images: this.imageList,
              videos: this.videoList,
              deviceId: this.device,
              deviceCategory: this.deviceType,
              id: this.localId
            })
              .then(() => {
                alert('无法连接到网络，数据已保存，可在【我提报的问题】中找到并提交数据')
                // navigate.open('my_fault')
                navigate.back()
              })
              .catch(msg => {
                this.$toast(msg)
              })
          } else {
            db.saveFault({
              profession: this.profession,
              time: this.time,
              line: this.line,
              station: this.station,
              address: this.address,
              reason: this.reason,
              type: this.type,
              level: this.level,
              source: this.source,
              content: this.content,
              images: this.imageList,
              videos: this.videoList,
              deviceId: this.device,
              deviceCategory: this.deviceType,
              id: new Date().getTime()
            })
              .then(() => {
                alert('无法连接到网络，数据已保存，可在【我提报的问题】中找到并提交数据')
                navigate.open('my_fault')
                // navigate.back()
              })
              .catch(msg => {
                this.$toast(msg)
              })
          }
        }
      }
    },

    handleSave () {
      if (this.profession == null || this.line == null || this.station == null || this.content == ''|| this.deviceType == '') {
        this.$toast.fail('请填写所有必填项')
      } else {

        if (this.localId) {
          db.updateMyFault({
            profession: this.profession,
            time: this.time,
            line: this.line,
            station: this.station,
            address: this.address,
            reason: this.reason,
            type: this.type,
            level: this.level,
            source: this.source,
            content: this.content,
            images: this.imageList,
            videos: this.videoList,
            deviceId: this.device,
            deviceCategory: this.deviceType,
            id: this.localId
          }).then(() => {
            alert('保存成功')
            navigate.open('my_fault')
          }).catch(msg => {
            this.$toast(msg)
          })
        } else {
          db.saveFault({
            profession: this.profession,
            time: this.time,
            line: this.line,
            station: this.station,
            address: this.address,
            reason: this.reason,
            type: this.type,
            level: this.level,
            source: this.source,
            content: this.content,
            images: this.imageList,
            videos: this.videoList,
            deviceId: this.device,
            deviceCategory: this.deviceType,
            id: new Date().getTime()
          }).then(() => {
            alert('保存成功')
            navigate.open('my_fault')
          }).catch(msg => {
            this.$toast(msg)
          })
        }
      }
    },

    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`;
      } else if (type === 'month') {
        return `${value}月`
      } else if (type === 'day') {
        return `${value}日`
      } else if (type === 'hour') {
        return `${value}时`
      } else if (type === 'minute') {
        return `${value}分`
      }
      return value;
    }
  }
}
</script>

<style lang="scss"></style>
